.container {
  position: relative;
}

.header {
  margin: 0 auto;
  max-width: 1920px;
  padding: 0 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 96px;
  box-shadow: 0px 0px 4px 0px rgba(120, 37, 141, 0.15);
}

.headerGroup {
  display: flex;
  gap: 55px;
}

.logo {
  width: 120px;
  height: 40px;
}

.options {
  list-style-type: none;
  display: flex;
  gap: 15px;
}

.link {
  padding: 10px 15px;
  cursor: pointer;
  color: #333333;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0.16px;
  transition: 0.5s;
  text-decoration: none;
}

.link:hover {
  color: #8e49a0;
  transition: 0.5s;
}

.list {
  margin-top: 20px;
  border: 1px solid #efefff;
  border-radius: 5px;
}

.item {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: #333;
  padding: 10px;
  border-bottom: 1px solid #efefff;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  transition: 0.5s;
}

.item:hover {
  cursor: pointer;
  color: #8e49a0;
  transition: 0.5s;
}

.item:last-child {
  border-bottom: none;
}
